<template>
  <el-main>
    <el-button @click="query" style="margin-bottom: 10px"></el-button>
    <el-table :data="queryInfo" style="width: 100%">
      <el-table-column  align="left" prop="CID" label=""></el-table-column>
      <el-table-column  align="center" prop="CName" label=""></el-table-column>
      <el-table-column  align="center" prop="CCredit" label=""></el-table-column>
      <el-table-column  align="right" prop="CTerm" label="" :formatter="TransType"></el-table-column>
    </el-table>
  </el-main>
</template>

<script>
import axios from 'axios'

export default {
  name: 'QueryCourses',
  data () {
    return {
      path: 'http://localhost:5000',
      api: '/QueryCourses',
      queryInfo: []
    }
  },
  methods: {
    query () {
      axios.get(this.path + this.api)
        .then(res => { console.log(res.data); this.queryInfo = res.data })
    },
    TransType (row, column) {
      return row.CTerm ? '' : ''
    }
  }

}
</script>

<style scoped>

</style>



