<template>
  <el-main>
    <el-table :data="queryInfo" style="width: 100%">
      <el-table-column align="left" prop="Username" label=""></el-table-column>
      <el-table-column align="center" prop="Password" label=""></el-table-column>
      <el-table-column align="center" prop="Usertype" label="" :formatter="TransType"></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="dialogFormVisible=true; usr=scope.row"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="usr">
        <el-form-item label=":" >
          <el-input v-model="usr.Username" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-input v-model="usr.Password"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-radio-group  v-model="usr.Usertype">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="updateUser"> </el-button>
      </div>
    </el-dialog>

  </el-main>
</template>

<script>
import axios from 'axios'

export default {
  name: 'UpdateDeleteUser',
  data () {
    return {
      path: 'http://localhost:5000',
      queryApi: '/QueryUsers',
      updateApi: '/UpdateUser',
      deleteApi: '/DeleteUser',
      dialogFormVisible: false,
      queryInfo: [],
      usr: {
        Username: '',
        Password: '',
        Usertype: 0
      }
    }
  },
  methods: {
    query () {
      axios.get(this.path + this.queryApi)
        .then(res => { console.log(res.data); this.queryInfo = res.data })
    },
    updateUser () {
      this.dialogFormVisible = false
      axios.post(this.path + this.updateApi, this.usr)
        .then(res => {
          if (res.data['State']) {
            this.query()
            this.$message({message: '', type: 'success'})
          } else {
            this.$message.error('')
          }
        })
    },
    deleteUser (index, row) {
      this.$confirm('')
        .then(_ => {
          axios.post(this.path + this.deleteApi, { Username: row.Username })
            .then(res => {
              if (res.data['State']) {
                this.query()
                this.$message({message: '', type: 'success'})
              } else {
                this.$message.error('')
              }
            })
        })
    },
    TransType (row, column) {
      switch (row.Usertype) {
        case 0: return ''
        case 1: return ''
        case 2: return ''
      }
    }
  },
  created () {
    this.query()
  }
}
</script>

<style scoped>

</style>



