<template>
  <el-main>
    <div>
      <p></p>
    </div>
    <el-form ref="form" :model="usr" label-width="80px">
      <el-form-item label=":">
        <el-input v-model="usr.Username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":">
        <el-input v-model="usr.Password" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":">
        <el-radio-group  v-model="usr.Usertype">
          <el-radio :label="0" ></el-radio>
          <el-radio :label="1"></el-radio>
          <el-radio :label="2" ></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="insert"></el-button>
      </el-form-item>
    </el-form>
  </el-main>
</template>

<script>
import axios from 'axios'

export default {
  name: 'InsertUser',
  data () {
    return {
      path: 'http://localhost:5000',
      api: '/InsertUser',
      usr: {
        Username: '',
        Password: '',
        Usertype: 0
      }
    }
  },
  methods: {
    insert () {
      axios.post(this.path + this.api, this.usr)
        .then(res => {
          if (res.data['State']) {
            this.$message({message: '', type: 'success'})
          } else {
            this.$message.error('')
          }
        })
    }
  }
}
</script>

<style scoped>

</style>



